import "./App.css";

import { useState } from "react";

import logo from "./assets/ids-nxt-logo.svg";
import {extractAppDataPath} from "./utils";

/**
 * Sends request to REST interface of VApp this website belongs to.
 * @param onSuccess { function } Function fetch response will be passed to as JSON object.
 */
function getAppData(onSuccess, onError) {
  let url = extractAppDataPath(window.location.pathname)
  fetch(url)
    .then((response) => response.json())
    .then((result) => onSuccess(result))
      .catch((error) => onError(error));
}

function App() {
  const [appData, setAppData] = useState();

  const updateAppData = () => getAppData(
      (response) => setAppData(response),
      (error)=>setAppData({...error, "Request": "Request failed!"})
  );

  return (
    <div className="App">
      <header className="App-header">
        <span>
          <img src={logo} className="App-logo" alt="IDS NXT logo" />
          <span>VApp demo website</span>
        </span>

        <p>This is an example website built with React.</p>

        <button onClick={updateAppData}>Fetch VApp data</button>
        {appData && (
          <table>
            <thead>
              <tr>
                <th>Key</th>
                <th>Value</th>
              </tr>
            </thead>
            <tbody>
              {Object.keys(appData).map((key) => (
                <tr key={key}>
                  <td>{key}</td>
                  <td>{JSON.stringify(appData[key])}</td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </header>
    </div>
  );
}

export default App;
